
import { defineComponent } from "vue";
const component = defineComponent({
    name: "Switch",
    emits: ["update:mode"],
    props: {
        mode: {
            type: Boolean,
            default: false
        }
    },
    setup(props, { emit }) {
        const toggle = () => {
            const newMode = !props.mode;
            emit("update:mode", newMode);
        };
        return { toggle };
    }
});
export default component;
