
import { defineComponent, onUnmounted } from "vue";

const component = defineComponent({
    name: "Toast",
    props: {
        msg: {
            type: String,
            required: true
        }
    },
    emits: ["close"],
    setup(_, { emit }) {
        const close = () => emit("close");
        const closer = setTimeout(close, 5000);
        onUnmounted(() => {
            clearTimeout(closer);
        });
        return { close };
    }
});

export default component;
